@font-face {
  font-family: 'OpenDyslexicRegular';
	src: url(OpenDyslexic-Regular.woff2);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

/* Outer Grid: DisksContainer and rotate buttons */
.ReactDisks {
  display: grid;
  grid-template: 1fr / 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  height: 100%;
}  

.DisksContainer {
  grid-row-start: 1;
  grid-column-start: 2;
}

.rotateClockwise {
  grid-row-start: 1;
  grid-column-start: 1;
  filter: drop-shadow(5px 2px 3px #ccc);
  transform: rotate(45deg);
}

.rotateCounterClockwise {
  grid-row-start: 1;
  grid-column-start: 3;
  filter: drop-shadow(-5px 2px 3px #ccc);
  transform: rotate(-45deg);
}

@media (orientation: portrait) {
  .ReactDisks {
    grid-template: 1fr 1fr 1fr / 1fr;
  }  

  .DisksContainer {
    grid-row-start: 2;
    grid-column-start: 1;
  }

  .rotateClockwise {
    grid-row-start: 1;
    grid-column-start: 1;
    transform: rotate(135deg);
  }

  .rotateCounterClockwise {
    grid-row-start: 3;
    grid-column-start: 1;
    transform: rotate(45deg);
  }
}

/* Inner Grid: Disks and their contents stacked on top of each other */
.DisksContainer, .ColumnsContainer {
  display: grid;
  justify-items: center;
  align-items: center;
}

.ColumnsContainer, svg {
  grid-row-start: 1;
  grid-column-start: 1;
  pointer-events: none;
}

.Disk {
  grid-row-start: 1;
  grid-column-start: 1;
}